import { Component, OnInit } from '@angular/core';
import { CommonService } from '../common.service';

@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html'
})
export class CommentsComponent implements OnInit {
  comments = [];  
  pager: any = {};
  pagedItems: any[];
  constructor(private cs:CommonService) { }

  ngOnInit() {
    this.cs.getComments().subscribe(data=>{this.comments=data; this.setPage(1);})
  }
  setPage(page: number) {
    if (page < 1 || page > this.pager.totalPages) {
        return;
    }

    // get pager object from service
    this.pager = this.cs.getPager(this.comments.length, page);

    // get current page of items
    this.pagedItems = this.comments.slice(this.pager.startIndex, this.pager.endIndex + 1);
}
}
